.hero {
  @include cover;
  &__outer {
    height: 100%;
    padding-top: 200px;
    padding-bottom: 50px;
    @include xxl {
      min-height: 100vh;
      padding-top: 125px;
      padding-bottom: 52px; }
    @include md {
      min-height: 100vh;
      padding-top: 140px;
      padding-bottom: 52px; }
    @include sm {
      padding-top: 170px;
      padding-bottom: 52px;
      margin-bottom: 76px; } }
  &__content {
    // width: 50%
    width: 100%;
    max-width: 854px;
    @include xxl {
      max-width: 754px; } }
  &__logo {
    display: block;
    margin-bottom: 100px;
    @include xxl {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 86px;
      max-width: 388px; }
    @include sm {
      max-width: 235px; }
    img {
      max-width: 100%;
      display: block; } }
  &__title {
    color: $textBlack;
    margin-right: -10px;
    margin-bottom: 43px;
    @include md {
      max-width: 600px;
      margin-bottom: 36px; }
    @include sm {
      margin-right: 0; } }
  &__descr {
    margin-bottom: 87px;
    line-height: 1.555;
    letter-spacing: -0.1px;
    color: $textLight;
    @include xxl {
      margin-bottom: 70px; }
    @include md {
      max-width: 450px;
      font-size: 16px;
      margin-bottom: 90px; }
    @include sm {
      line-height: 1.67;
      margin-bottom: 66px; } }
  &__actions {
    display: flex;
    align-items: center;
    font-size: 0;
    @include md {
      flex-direction: column;
      align-items: flex-start; }
    @include sm {
      align-items: center;
      margin-left: -36px; } }
  &__question-btn {
    margin-right: 93px;
    @include xl {
      margin-right: 65px; }
    @include md {
      margin-bottom: 50px;
      margin-right: 0; }
    @include sm {
      margin-bottom: 46px; } } }
